<template>
  <nav @mouseleave="onleave" class="top-bar row">
    <div class="title-area columns small-2">
      <div class="icon__group">
        <router-link to="/">
          <h3>MAGNET</h3>
          <span class="sub-title">magnet ventures</span>
        </router-link>
      </div>
    </div>
    <a @mousemove="onhover" @mouseleave="onleave" href="#" class="title__menu columns align-center small-7">
      <img class="title__hamburger" :src="hamburgerImage" alt="">
      <ul class="nav--level-1-menu">
        <li v-for="menuItem in menu" class="nav--menu-item" :data-key="menuItem.key" :data-second-level="!!menuItem.subMenu">
          <router-link :to="`/${menuItem.key.toLowerCase()}`" class="nav__menu-alink">{{menuItem.key}}</router-link>
          <ul v-if="!!menuItem.subMenu" class="nav--level-2-menu">
            <li v-for="menuItem2 in menuItem.subMenu" class="nav--menu-item" :data-key="menuItem2.key">
              <a href="#">{{menuItem2.key}}</a>
            </li>
          </ul>
        </li>
      </ul>
    </a>
  </nav>
</template>

<script>
import HamburgerImage from '../assets/hamburger-menu.png';

export default {
  name: 'NavBar',
  props: {
    hamburgerImage: {
      type: String,
      default: HamburgerImage
    }
  },
  data() {
    return {
      checkedKey: null,
      checkedKey2: null,
      secondLevel: false,
      menu: [
        {
          key: 'PHILOSOPHY'
        },
        {
          key: 'TEAM'
        },
        {
          key: 'PORTFOLIO'
        },
        {
          key: 'CONTACT'
        }
      ],
    }
  },
  methods: {
    onhover(e) {
      e.stopPropagation();
      const menu = document.querySelectorAll('.nav--level-1-menu')[0];
      const className = menu.className;
      if (e.target.className.indexOf('nav__menu-alink') < 0) {
        if (className.indexOf('hover') < 0) {
          menu.className += ' hover';
        } else {
          menu.className = menu.className.replace(' hover', '');
        }
      }
    },
    onleave(e) {
      const menu = document.querySelectorAll('.nav--level-1-menu')[0];
      menu.className = menu.className.replace(' hover', '');
    },
    // onhover2(e) {
    //   e.stopPropagation();
    //   this.secondLevel = true;
    //   if (e.target.tagName === 'A') {
    //     this.checkedKey2 = e.target.parentNode.dataset.key;
    //   } else if (e.target.tagName === 'LI') {
    //     this.checkedKey2 = e.target.dataset.key;
    //   } else {
    //     this.checkedKey2 = null;
    //   }
    // }
  }
}
</script>

<style lang="scss">
@import '../App.scss';

@media screen and (max-width: 720px) {
  .top-bar.row {
    align-items: flex-start;
    flex-direction: column;
    .title-area {
      max-width: 100%;
      width: 100%;
    }
    .title__menu {
      align-self: stretch;
      max-width: 100%;
      padding: 0;
    }
    .nav--level-1-menu {
      display: none;
      padding: 0;
      background: rgb(37, 37, 37);
      &.hover {
        display: block;
      }
    }
    .nav--menu-item {
      height: rem-calc(55);
      line-height: rem-calc(55);
      display: block;
    }
    .title__hamburger {
      position: absolute;
      top: rem-calc(13);
      right: rem-calc(10);
      display: block;
      width: rem-calc(30);
      height: rem-calc(23);
    }
  }
}

.top-bar {
  background-color: #000;
  &.row {
    max-width: 100%;
    position: fixed;
    top: 0;
    z-index: 10;
    width: 100%;
  }
  .title__hamburger {
    display: none;
  }
  ul {
    list-style-type: none;
    margin-left: 0;
  }
  a {
    &,
    &:hover,
    &:focus,
    &:active {
      color: white;
    }
  }
  .title-area {
    padding: rem-calc(5) rem-calc(20);
    color: white;
    h3 {
      margin-bottom: rem-calc(-10);
    }
    span {
      font-size: rem-calc(12);
    }
  }
  .icon__group {
    float: left;
  }
  .nav--level-1-menu {
    margin-bottom: 0;
  }
  .nav--level-2-menu {
    display: none;
    position: absolute;
    top: rem-calc(39);
    left: rem-calc(-6);
    height: rem-calc(40);
    line-height: rem-calc(40);
    color: white;
    width: rem-calc(400);
    &:before {
      content: "";  /*:before和:after必带技能，重要性为满5颗星*/
      display: block;
      position: absolute;  /*日常绝对定位*/
      background: red;
      top: rem-calc(-20);
      width: 0;
      height: 0;
      border: rem-calc(10) solid transparent;
      left: rem-calc(20);
      border-left-color: #000;
      border-right-color: #000;
      border-top-color: #000;
    }
    .nav--menu-item {
      float: left;
      &.active,
      .router-link-active {
        a {
          color: #000;
        }
      }
    }
  }
  .nav--menu-item {
    position: relative;
    display: inline-block;
    font-size: rem-calc(14);
    padding: rem-calc(26) rem-calc(5);
    height: 100%;
    @media screen and (max-width: 480px) {
      padding: 0;
    }
    &.active {
      ul {
        display: block;
      }
      &>a {
        color: red;
      }
    }
    .router-link-active {
      color: red;
    }
    .nav__menu-alink {
      padding: rem-calc(26) 0;
      @media screen and (max-width: 480px) {
        padding: 0;
        line-height: rem-calc(55);
        width: 100%;
        display: inline-block
      }
    }
  }
}
</style>


