<template>
  <div class="page__philosophy">
    <section class="section__card section__philosophy section--1 row">
      <div class="block__image">
        <img :src="philosophyImg" alt="">
      </div>
      <p class="block__detail">
        {{philosophyGoal}}
      </p>
    </section>
    <section class="section__card section__philosophy section--2 row">
      <div class="columns small-7">
        <div class="block__image">
          <img :src="philosophyLogo" alt="">
        </div>
      </div>
      <div class="columns small-5">
        <div class="block__image">
          <img :src="philosophyEarth" alt="">
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import philosopyImg from '../assets/philosophy.png';
import philosophyEarth from '../assets/philosophy-earth.png';
import philosophyLogo from '../assets/philosophy-logo.png';
export default {
  name: 'PhilosophyPage',
  props: {
    philosophyImg: {
      type: String,
      default: philosopyImg
    },
    philosophyGoal: {
      type: String,
      default: '致力成为投资界的思考着与科技革命的助推者'
    },
    philosophyEarth: {
      type: String,
      default: philosophyEarth
    },
    philosophyLogo: {
      type: String,
      default: philosophyLogo
    }
  },

}
</script>

<style lang="scss">
@import '../App.scss';
.page__philosophy {
  .section--1 {
    background: rgb(221, 221, 221);
    .block__image {
      margin-top: 15%;
      margin-left: 10%;
      margin-right: 10%;
      float: left;
    }
    .block__detail {
      width: 100%;
      text-align: center;
      float: left;
      margin-top: rem-calc(40);
    }
  }
  .section--2 {
    display: flex;
    background: rgb(28, 26, 27);
    .block__image {
      margin-top: 30%;
    }
  }
}
.section__card {
  display: block;
  width: 100%;
  height: rem-calc(580);
  text-align: left;
}
</style>


