<template>
  <div class="page__portfolio">
    <carousel :perPage="1" :autoplay="true" :autoplayHoverPause="true" :autoplayLoop="true" class="gallery__slide">
      <slide v-for="(slideItem, index) in slides" :key="index" class="carousel__slide">
        <div class="vertical--center">
          <img :src="slideItem.imgUrl" alt="">
          <router-link :to="slideItem.pageUrl" class="button button__view-all">View Detail</router-link>
        </div>
      </slide>
    </carousel>
  </div>
</template>
<script>
import { Carousel, Slide } from 'vue-carousel';

export default {
  name: 'Portfolio',
  props: {
    slides: {
      type: Array,
      default() {
        return [
          {
            imgUrl: '/static/innfos.png',
            pageUrl: '/portfolio/innfos'
          },
          {
            imgUrl: '/static/mole.png',
            pageUrl: '/portfolio/mole'
          },
          {
            imgUrl: '/static/parksmart.png',
            pageUrl: '/portfolio/parksmart'
          }
        ]
      }
    }
  },
  mounted() {
    this.setSlideSize();
    var timer = 1;
    window.onresize = () => {
      if (timer) {
        clearTimeout(timer);
        timer = setTimeout(() => {
          this.setSlideSize();
        }, 400);
      }
    }
  },
  methods: {
    setSlideSize() {
      const innerHeight = window.innerHeight;
      const innerWidth = window.innerWidth;
      const headerDOM = document.querySelectorAll('.top-bar');
      const slideDOM = document.querySelectorAll('.carousel__slide');
      const gallerySlideDOM = document.querySelectorAll('.gallery__slide');
      const menuDOM = document.querySelectorAll('.title__menu');
      var menuHeight = menuDOM[0].clientHeight;
      if (innerWidth > 720) {
        menuHeight = 10;
      }
      const headerHeight = headerDOM[0].clientHeight;
      gallerySlideDOM[0].style.width = innerWidth + 'px';
      slideDOM.forEach((s) => {
        s.style.height = innerHeight - headerHeight + menuHeight + 'px';
      });
    }
  },
  components: {
    carousel: Carousel,
    slide: Slide
  }
}
</script>

<style lang="scss">
@import '../App.scss';

.page__portfolio {
  .gallery__slide {
    background: rgb(12, 11, 16);
  }
  .VueCarousel-dot-inner {
    background: transparent !important;
    border: 2px solid #ccc;
  }
  .VueCarousel-dot--active {
    .VueCarousel-dot-inner {
      background: white !important;
      border: 2px solid #fff;
    }
  }
  .carousel__slide {
    padding-top: rem-calc(67);
    img {
      width: 100%;
    }
    .button {
      margin-top: rem-calc(10);
    }
  }
}

</style>


