<template>
  <div>
    <section1></section1>
    <section2></section2>
    <section3></section3>
    <section4></section4>
  </div>
</template>

<script>
import section1 from '@/components/Mainpage-section1';
import section2 from '@/components/Mainpage-section2';
import section3 from '@/components/Mainpage-section3';
import section4 from '@/components/Mainpage-section4';
export default {
  mounted() {
    this.setSlideSize();
    var timer = 1;
    window.onresize = () => {
      if (timer) {
        clearTimeout(timer);
        timer = setTimeout(() => {
          this.setSlideSize();
        }, 400);
      }
    }
  },
  methods: {
    setSlideSize() {
      const innerHeight = window.innerHeight;
      const innerWidth = window.innerWidth;
      const headerDOM = document.querySelectorAll('.top-bar');
      const headerHeight = headerDOM[0].clientHeight;
      const slideDOM = document.querySelectorAll('.carousel__slide');
      slideDOM.forEach((s) => {
        s.style.height = innerHeight - headerHeight + 10 + 'px';
      });
    }
  },
  components: {
    section1,
    section2,
    section3,
    section4
  }
}
</script>

<style lang="scss">
@import '../App.scss';
.vertical--center {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  margin-left: rem-calc(30);
  margin-right: rem-calc(30);

  @media screen and (max-width: 480px) {
    margin-left: rem-calc(0);
    margin-right: rem-calc(0);
    padding-left: rem-calc(10);
    padding-right: rem-calc(10);
  }
}

.title--underline {
  border-bottom: 2px solid #8526b2;
  color: #252525;
  display: inline-block;
  font-size: 1.8554688vw;
  font-weight: 700;
  margin-bottom: rem-calc(30);
  text-transform: uppercase;
  a {
    color: inherit;
    font-weight: inherit;
    text-decoration: none;
  }

  @media screen and (max-width: 480px) {
    font-size: rem-calc(16);
  }
}
.title--xlarge {
  color: #252525;
  font-size: 10.6484375vw;
  font-weight: 900;
  line-height: 0.9;
  @media screen and (max-width: 480px) {
    font-size: rem-calc(30);
  }
}
.title--large {
  font-size: 5.46875vw;
  font-weight: 400;
}
.title--grey {
  color: #666;
}
.title--medium {
  font-size: 2.1484375vw;
  @media screen and (max-width: 480px) {
    font-size: rem-calc(18);
  }
}
.text__description {
  padding: rem-calc(80) 0;

}

.carousel__slide {
  display: block;
  width: 100%;
  text-align: left;
}

.gallery__slide {
  display: block;
  float: left;
  background: url('../assets/home_slide_background.png') no-repeat center center transparent;
  background-size: auto 100%;
}
</style>


