<template>
  <div @mouseleave="onleave" class="page__team row">
    <router-view></router-view>
    <aside class="columns small-3 aside-list__aside">
      <section class="aside-list">
        <a href="#" @mousemove="onhover" @mouseleave="onleave">
          <h4 class="aside-list__header title--underline">
            INVESTMENT PARTNERS
          </h4>
          <ul class="aside-list__ul">
            <li class="aside-list__li" v-for="user in investmentPartners">
              <router-link :to="`/team/${user.url}`" class="aside-list__alink">{{user.name}}</router-link>
            </li>
          </ul>
        </a>
      </section>
    </aside>
  </div>
</template>
<script>
export default {
  name: 'TeamPage',
  props: {
    investmentPartners: {
      type: Array,
      default() {
        return [
          {
            url: 'georgia-lu',
            name: 'Georgia Lu'
          },
          {
            url: 'yanan-zhao',
            name: 'Yanan Zhao'
          },
          {
            url: 'zhenyu-yin',
            name: 'Zhenyu Yin'
          },
          {
            url: 'ji-liu',
            name: 'Ji Liu'
          },
          {
            url: 'ke-bai',
            name: 'Ke Bai'
          }
        ]
      }
    },
  },
  methods: {
    onhover(e) {
      e.stopPropagation();
      const menu = document.querySelectorAll('.aside-list__ul')[0];
      const className = menu.className;
      if (e.target.className.indexOf('aside-list__alink') < 0) {
        if (className.indexOf('hover') < 0) {
          menu.className += ' hover';
        } else {
          menu.className = menu.className.replace(' hover', '');
        }
      }
    },
    onleave(e) {
      const menu = document.querySelectorAll('.aside-list__ul')[0];
      menu.className = menu.className.replace(' hover', '');
    },
  }
}
</script>

<style lang="scss">
@import '../App.scss';
.page__team {
  margin-top: rem-calc(67);

  @media screen and (max-width: 480px) {
    margin-top: rem-calc(50);
    .aside-list__aside {
      position: fixed;
      width: 100%;
      max-width: 100%;
      padding: 0;
    }
  }
}

.section__team {
  display: flex;
  background: #fff;
  height: rem-calc(580);
  .title--large {
    color: #252525;
    font-size: 60px;
    font-weight: 900;
    line-height: 1.1;
    padding-left: rem-calc(45);
    @media screen and (max-width: 480px) {
      font-size: rem-calc(27);
      padding-left: 0;
      width: 100%;
    }
  }
  .button__view-video {
    margin-top: rem-calc(40);
    margin-left: rem-calc(45);
  }
  .vertical--center {
    margin-top: rem-calc(70);
  }
  @media screen and (max-width: 480px) {
      max-width: 100% !important;
      flex: 0 0 100% !important;
      display: block;
  }
}

.aside-list {
  margin-top: rem-calc(50);
  text-align: left;
  .title--underline {
    border-bottom: 3px solid #8526b2;
    color: #252525;
    display: inline-block;
    font-size: 1.655469vw;
    font-weight: 800;
  }
  @media screen and (max-width: 480px) {
    text-align: center;
    margin-top: 0;
    padding-top: rem-calc(10);
    background: rgb(247, 247, 247);
  }
}

.aside-list__li {
  list-style: none;
  .router-link-active {
    color: #000;
  }
}
.aside-list__header {
  margin-bottom: rem-calc(15);
  padding-bottom: rem-calc(6);

  @media screen and (max-width: 480px) {
    font-size: rem-calc(18) !important;
    width: 100%;
    margin-bottom: 0;
  }
}

.aside-list__ul {
  margin-left: 0;
  a {
    color: #8526b2;
    font-size: 20px;
    font-weight: 400;
    line-height: 1.5;
  }
  @media screen and (max-width: 480px) {
    display: none;
    &.hover {
      display: block;
    }
  }
}

</style>



